    font-size: 16px;
    margin-top: 40%;
    text-shadow: 1px 1px 1px rgba(150, 150, 150, 0.5);
}

@media only screen and (max-width: 1024px) {
  
}

@media only screen and (max-width: 640px) {
    .weather {
        margin-top: 60%;
    }
}
.search {
    grid-column-start: 2;
    grid-row-start: 3;
    width: 100%;
    height: 40px;
    margin: 0 auto;
    margin-top: 25%;
    background: #444;
    background: rgba(0,0,0,.2);
    border-radius:  3px;
    border: 1px solid #fff;
    font-family: 'Roboto Condensed', sans-serif;
}
    
.search input {
      width: 51.5%;
      padding: 10px 2px;
      color: #fff;
      font-size: 16px;
      border: 0;
      background: transparent;
      border-radius: 3px 0 0 3px;
}

.search label {
      width: 25%;
}

.search input:focus  {
        outline: 0;
        background:transparent;
      }

.search input:-webkit-autofill,
.search input:-webkit-autofill:hover,
.search input:-webkit-autofill:focus,
.search input:-webkit-autofill:active {
          -webkit-transition: background-color 5000s ease-in-out 0s;
          transition: background-color 5000s ease-in-out 0s;
      }

.search button {
      float:right;
      border: 0;
      padding: 0;
      cursor: pointer;
      height: 40px;
      width: 30%;
      color: #fff;
      background: transparent;
      border-left: 1px solid #fff;
      border-radius: 0 3px 3px 0;
      font-family: 'Roboto Condensed', sans-serif;
      font-size: 15px;
}

.search button:hover {
        background: #fff;
        color:#444;
      }

.search button:active {
        box-shadow: 0px 0px 12px 0px rgba(225, 225, 225, 1);
      }

.search button:focus {
        outline: 0;
      }

@media only screen and (max-width: 1024px) {
      .search button {
            border: 1px solid #fff;
            margin-top: 3%;
            float: none;
      }

}

@media only screen and (max-width: 640px) {
      .search button {
            margin-top: 6%;
            float: none;
      }
      
}


@media only screen and (max-width: 455px) {
      .search button {
            width: 40%;
      }
}
.error {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 16px;
    
    grid-column-start: 2;
    grid-row-start: 4;
}

@media only screen and (max-width: 1024px) {
  
}
.btn {
    grid-column-start: 3;
    grid-row-start: 1;
    margin: 5% auto;
    border: 0;
    padding: 0;
    color: #fff;
    background: transparent;
    border: 1px solid #fff;
    border-radius: 3px 3px;
    height: 25px;
    width: 45px;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 14px;
    text-shadow: 1px 1px 1px rgba(150, 150, 150, 0.5);
    outline: 0;
}
.btn:hover {
	background: #fff;
    color:#444;
    outline: 0;
}
.btn:active {
    box-shadow: 0px 0px 12px 0px rgba(225, 225, 225, 1);
    outline: 0;
}

@media only screen and (max-width: 1024px) {
  
}

@media only screen and (max-width: 640px) {
    .btn {
        margin: 10% auto;
    }
}

.App {
  text-align: center;
  width: 100%;
  height: 95vh;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  color: #FFFFFF;
  font-family: 'Roboto Condensed', sans-serif;
  background-size: cover;
  background-position: top;
}

.cityName {
  font-size: 35px;
  grid-row-start: 2;
  grid-column: 2/3;
  text-shadow: 1px 1px 1px rgba(150, 150, 150, 0.5);
}


@media only screen and (max-width: 1024px) {

}

@media only screen and (max-width: 640px) {
  .cityName {
      margin: 0 auto;
  }
}
